body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.box{
  position: absolute;

  cursor: pointer;

  height: auto;
  width: auto;
}


.box input[type="button"]{
  background: white;
  color: #3D3D3D;
  padding: 5px;
  width: 200px;
  height: 40px;
  border: 1px solid #3D3D3D;
  border-radius: .5rem;
  font-size: 12px;

  text-align: left;
  -webkit-appearance: button;
  outline:none;
  
  cursor: pointer;
}

.box::before{
  content: "\F0D7";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  font-size: 20px;
  
  position:absolute;
  line-height: 40px;
  color:white;

  text-align:center;
  top:0;
  right:0;
  
  width:40px;
  height:38px;
  border: 1px solid #318989;
  border-top-right-radius:.5rem;
  border-bottom-right-radius:.5rem;
  background: #318989;
}


.box:focus{
  background-color: RED; 
}

.box > option{
  width: 100px;
}

.optionSelect{
  display:none;
}

.optionSelect.active{
  display: block;
}

.selectOption{
  position: absolute;

  width: 100%;
  height: auto;

  background-color: WHITE;

  padding: 10px;
  padding-left: 0;
  padding-right: 0;
  border-radius: .5rem;

  color: #3D3D3D;
  box-shadow: 0px 0px 17px 0px rgba(131,131,131,1);
}

.selectOption button{
  width: 100%;
  height: 30px;

  background-color: inherit;
  color: inherit;

  border: 0;

  text-align: left;
}

.selectOption button:hover{
  cursor: pointer;

  background-color: #C3E6E6;
}


